.sideText {
  display: flex;
  flex-direction: column;
  margin-left: 50px;
  color: black;
  height: 80%;
  justify-content: space-between;
  width: 30%;
  line-height: 1.6;
}

.activeContainer {
  display: flex;
  align-items: center;
  margin-bottom: 50%;
}
input[type="checkbox"] {
  transform: scale(2);
  -ms-transform: scale(2);
  -webkit-transform: scale(2);
  padding: 10px;
}

li {
  font-size: 24px;
  padding: 5px;
  font-family: monospace;
}

.activeItem {
  display: flex;
  align-items: center;
}

.activeContainer img {
  padding-left: 10px;
}

@media (min-width: 320px) and (max-width: 1200px) {
  .sideText {
    display: none;
  }
}
